import React, { FC, useState, useRef } from 'react'
import { Block } from 'views/components/UI/content'
import { Form, Input, Typography, Divider, Select, DatePicker, Button, Result } from 'antd'
import { createEntrepreneur, updateEntrepreneur } from 'services/entrepreneur'
import { useHistory } from 'react-router-dom'

const FacilitatorForm: FC = () => {
  const [status, setStatus] = useState('')
  const currentUser = useRef<any>({})
  const history = useHistory()
  const handleSubmit = (values: any): void => {
    currentUser.current = values
    createEntrepreneur({
      provider: 'local',
      username: values.email.split('@')[0],
      email: values.email,
      password: values.identification,
    })
      .then(({ data: userData }: any) => {
        if (userData.user) {
          const userId = userData.user.id
          currentUser.current.id = userId
          updateEntrepreneur({
            id: userId,
            ...values,
          })
            .then(() => {
              setStatus('finished')
            })
            .catch(() => {
              setStatus('error')
            })
        }
      })
      .catch(() => {
        setStatus('error')
      })
  }

  const createMore = (): void => {
    setStatus('')
  }

  const goToList = (): void => {
    history.go(-1)
  }

  if (status === 'error') {
    const button = (
      <Button onClick={goToList} type="primary" key="console">
        Volver atrs
      </Button>
    )
    return (
      <Result
        status="warning"
        title="Hubo en error durante la creacin del emprendedor"
        subTitle="Es posible que ya exista un usuario con los el mismo email"
        extra={button}
      />
    )
  }

  if (status === 'finished') {
    return (
      <Result
        status="success"
        title="Usuario creado exitosamente"
        subTitle={`El usuario: ${currentUser.current.names} ${currentUser.current.last_names} fue creado con el ID: ${currentUser.current.id}`}
        extra={[
          <Button onClick={goToList} type="primary" key="console">
            Hecho
          </Button>,
          <Button onClick={createMore} key="buy">
            Seguir Creando
          </Button>,
        ]}
      />
    )
  }

  return (
    <Block flex={1} width="50%" height="100%" margin="0 auto">
      <Typography.Title>Nuevo Emprendedor</Typography.Title>
      <Divider />
      <Form onFinish={handleSubmit} layout="vertical">
        <Form.Item
          label="Nombres"
          name="names"
          rules={[{ required: true, message: 'Este campo es requerido' }]}>
          <Input />
        </Form.Item>
        <Form.Item
          label="Apellidos"
          name="last_names"
          rules={[{ required: true, message: 'Este campo es requerido' }]}>
          <Input />
        </Form.Item>
        <Form.Item
          label="Cdula"
          name="identification"
          rules={[{ required: true, message: 'Este campo es requerido' }]}>
          <Input type="number" />
        </Form.Item>

        <Form.Item
          label="Tipo"
          name="condition"
          rules={[{ required: true, message: 'Este campo es requerido' }]}>
          <Select>
            <Select.Option value="Egresado">Egresado</Select.Option>
            <Select.Option value="Estudiante">Estudiante</Select.Option>
            <Select.Option value="Otro">Otro</Select.Option>
          </Select>
        </Form.Item>
        <Form.Item label="Cdigo" name="studentCode">
          <Input type="number" />
        </Form.Item>
        <Form.Item
          label="Correo"
          name="email"
          rules={[
            {
              type: 'email',
              message: 'Correo no vlido',
            },
            {
              required: true,
              message: 'Profavor ingresa tu correo electrnico',
            },
          ]}>
          <Input type="email" />
        </Form.Item>

        <Form.Item label="Carrera" name="career">
          <Select>
            <Select.OptGroup key="uno" label="Ingeniera">
              <Select.Option value="Ingeniera informtica">Ingeniera informtica</Select.Option>
              <Select.Option key="uno-02" value="Ingeniera multimedia">
                Ingeniera multimedia
              </Select.Option>
              <Select.Option key="uno-03" value="Ingeniera mecnica">
                Ingeniera mecnico
              </Select.Option>
              <Select.Option key="uno-04" value="Ingeniera mecatrnica">
                Ingeniera mecatrnica
              </Select.Option>
              <Select.Option key="uno-05" value="Ingeniera industrial">
                Ingeniera industrial
              </Select.Option>
              <Select.Option key="uno-01" value="Ingeniera biomdica">
                Ingeniera biomdica
              </Select.Option>
            </Select.OptGroup>
            <Select.OptGroup key="dos" label="Ciencias Administrativas">
              <Select.Option key="dos-2" value="Administracin de empresas">
                Administracin de empresas
              </Select.Option>
              <Select.Option value="Comercio y negocios internacionales">
                Comercio y negocios internacionales
              </Select.Option>
              <Select.Option value="Contaduria pblica">Contaduria pblica</Select.Option>
            </Select.OptGroup>
            <Select.OptGroup key="tres" label="Comunicacin y ciencias sociales">
              <Select.Option value="Comunicacin social y periodismo">
                Comunicacin social y periodismo
              </Select.Option>
              <Select.Option value="Economa">Economa</Select.Option>
            </Select.OptGroup>
            <Select.OptGroup key="cuatro" label="Ciencias bsicas">
              <Select.Option value="Administracin ambiental">
                Administracin ambiental
              </Select.Option>
            </Select.OptGroup>
            <Select.OptGroup key="cinco" label="Humanidades y Arte">
              <Select.Option value="Cine y comunicacin digital">
                Cine y comunicacin digital
              </Select.Option>
              <Select.Option value="Comunicacin publicitaria">
                Comunicacin publicitaria
              </Select.Option>
              <Select.Option value="Diseo de la comunicacin grfica">
                Diseo de la comunicacin grfica
              </Select.Option>
            </Select.OptGroup>
          </Select>
        </Form.Item>
        <Form.Item
          label="Celular"
          name="phone"
          rules={[{ required: true, message: 'Este campo es requerido' }]}>
          <Input type="number" maxLength={10} />
        </Form.Item>
        <Form.Item label="Direccin" name="address">
          <Input />
        </Form.Item>
        <Form.Item
          label="Fecha de nacimiento"
          name="birthday"
          rules={[{ required: true, message: 'Este campo es requerido' }]}>
          <DatePicker />
        </Form.Item>
        <Form.Item>
          <Button htmlType="submit" type="primary">
            Enviar
          </Button>
        </Form.Item>
      </Form>
    </Block>
  )
}
export default FacilitatorForm
